// stylelint-disable at-rule-empty-line-before

@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
@import "helpers";

.wrapper {
	display: flex;
	flex-direction: column;
	justify-content: center;
	max-width: 660px; 
	margin: 0 auto;
	box-sizing: content-box;
	min-height: 100vh;
}

//CONTACTS

.contacts {
	display: flex;
	position: relative;
	flex-direction: row;
	max-width: 660px;
	background-image: $background-image;

	@include mobile {
		flex-direction: column;
		align-items: center;
	}
	
	&:after {
		position:absolute;
		right: 4px;
		bottom: 11px;
		display: inline-block;
		content: "";
		background-image: url("../images/approved.png");
		background-repeat: no-repeat;
		background-position: bottom;
		background-size:100%;
		width: 27.87%;
		height: 166px;
	}
}

.contacts__profile {
	width: 138px;
	height: 138px;
	border-radius: 100px;
	margin: 30px 2px 82px 20px;

	@include mobile {
		width: 250px;
		height: 250px;
		border-radius: 200px;
		margin: 30px 0 0 0;
	}
}

.contacts__items {
	flex-grow: 1;
	width: 100%;
	max-width: 460px;
	padding: 0 20px;
	box-sizing: border-box;

	@include mobile {
		margin-bottom: 40px;
	}
}

.contacts__item {
	height: 53px;
	font-size: 12px;
	text-transform: uppercase;
	color: #666666;
	letter-spacing: 1.2px;
	border-bottom: 2px solid black;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;

	p {
		margin: 0;
		padding-bottom: 8px;
	}
}

//SKILLS

.skills {
	max-width: 660px;
	background-image: $background-image;
	margin-top: 2px;
}

.skills__text {
	margin-top: 2px;
	padding: 34px 0 2px 20px;
}

.skills__items {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	justify-content: space-around;
	font-size: 12px;
	padding: 5px 0 0 20px;

	@include mobile-s {
		padding-right: 20px;
	}
}

.skills__item {
	width: 145px;
	color: #666666;
	font-weight: 500;
	letter-spacing: 1.2px;
	text-transform: uppercase;
	margin-bottom: 47px;
	display: flex;
	flex-direction: row;
	align-items: center;

	@include mobile-s {
		width: 100px;
	}
}

label {
	padding-left: 6px;
}

//SCALE

.scale {
	max-width: 660px;
	margin-top: 2px;
	background-image: $background-image;
	display: flex;
	flex-direction: row;
	align-items: flex-start;

	@include mobile {
		flex-direction: column;
		align-items: center;
		text-align: center;
	}
}

.scale__image {
	padding: 52px 68px 61px 20px;
	position: relative;

	@include mobile {
		padding-top: 40px;
		padding-right: 20px;
	}
}

@keyframes in {
	from {
		transform: rotateZ(-33deg);
	}
	to {
		transform: rotateZ(80deg);
	}
}

.scale__arrow {
	position: absolute;
	left: 103px;
	bottom: 59px;
	transform: rotate(-33deg);
	-webkit-animation: in 5s linear;
	animation-timing-function: ease-out;
	animation-direction: normal;
	animation-fill-mode: forwards;
	transform-origin: 40px 26px;
}

.scale__text h3 {
	margin: 0;
	line-height: 24px;
	padding: 57px 0 10px 0;

	@include mobile {
		padding: 0;
	}
}

.scale__number {
	margin: 0;
	font-weight: 900;
	font-size: 50px;
	color: #0093d7;
}
